.ButtonsInline {
  position: relative;
  height: 50px;
  margin-top: 25px;

  & > button, & > a {
    &:nth-child(1) {
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }

    &:nth-child(2) {
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}