.Select01 {
  width: calc(100% - 24px);
  border: 1px solid #DADCE0;
  padding: 7px;
  border-radius: 5px;
  transition: all .12s;
  position: relative;
  margin: 5px;
  margin-top: 12px;

  & > select {
    border: none;
    outline: none;
    width: 100%;

    &:valid + small {
      top: 0;
      font-size: 12px;
    }
  }

  &:focus-within {
    box-shadow: 0px 0px 0px 1px inset #5EC636;
    border: 1px solid #5EC636;

    & > small {
      top: 0;
      font-size: 12px;
      color: #5EC636;
    }
  }

  & > small {
    transition: all .12s;
    pointer-events: none;
    position: absolute;
    top: 50%;
    left: 6px;
    font-size: 14px;
    transform: translateY(-50%);
    background-color: #ffffff;
    padding: 3px;
    color: #282c34;
  }
}