.Link__transparant {
  text-decoration: none;
  cursor: pointer;

  padding: 12px;
  border-radius: 2px;
  color: #5EC636;
  font-size: 14px;
  font-weight: bold;
  transition: all .12s;

  &:hover {
    background-color: rgba(121, 234, 79, 0.14);
  }

  &:active {
    background-color: rgba(121, 234, 79, 0.26);
  }
}

.Link__solid {
  text-decoration: none;
  cursor: pointer;

  background-color: #5EC636;
  padding: 12px;
  border-radius: 4px;
  color: #ffffff;
  font-size: 14px;
  font-weight: bold;
  transition: all .12s;

  &:hover {
    background-color: #5cb847;
  }

  &:active {
    background-color: #58b344;
  }
}