.CenterLayout {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: calc(80vw - 24px);
  border-radius: 5px;
  border: 1px solid #DADCE0;
  padding: 24px;

  @media only screen and (min-width: 1000px) {
    width: fit-content;
  }

  @media only screen and (max-width: 1000px) {
    .CenterLayout__inner {
      & > span {
        width: 100%;

        &:nth-child(2) {
          display: none;
        }
      }
    }
  }

  .CenterLayout__inner {
    & > span {
      display: inline-block;
      vertical-align: middle;
      text-align: left;

      & > h2 {
        font-size: 24px;
        margin: 6px;
        color: #323232;
        margin-bottom: 18px;
        font-weight: normal;
      }

      & > p {
        font-size: 13px;
        margin: 6px;
        color: #323232;
      }
    }
  }
}